/*--------------------------------------------------------------
# Page Templates
--------------------------------------------------------------*/

// Global - pages with sidebar
body{
  // for .blog, .single-post .page .error404, .search-results
  // = pages without sidebar
  &:not(.home):not(.page-template-about):not(.single-product):not(.post-type-archive):not(.tax-product-type):not(.post-type-archive-adventure):not(.single-adventure) {
    .site-content{
      width: 1120px;
      margin: 0 auto;
      padding-top: 50px;

      display: flex;

      .content-area{
        flex-basis: 70%;
        padding: 60px 50px 60px 0;
      }
      .widget-area{ // sidebar
        flex-basis: 30%;
        padding-top: 20px;
        padding-left: 60px;
        border-left: $solid_border;
      }  
    } // end of .site-content 
  }   
} // end of body


/* Journal page and Singal post page, 
also Category page, Tag page, Month archives page - post style*/
.blog, .single-post, .archive{
  .post{
    margin-bottom: 80px;
    .entry-header{
      position: relative;
      img{
        width: 100%;
        display: block;
      }
      .entry-title{
        position: absolute;
        left: 0;
        top: 8px;
        background-color: $color_primary_green;
        width: 570px;
        padding: 15px 25px;
        line-height: 40px;
        a{
          color: white;
          font-size: 2rem;
          font-weight: 700;
          &:hover{
            color: $color_primary_yellow;
          }
        }
        &:not(.blog){
          color: white;
          font-size: 2rem;
          font-weight: 700;
        }
      }
      .entry-meta{
        position: absolute;
        right: 0;
        bottom: 0;
        background-color: $color_primary_green;
        padding: 5px 15px;
        color: white;
        font-family: $font_accent;
        text-transform: uppercase;
        span{
          color: white;
          font-family: $font_accent;
          text-transform: uppercase;
          &:not(.blog){
            a{
              color: white;
              font-family: $font_accent;
              text-transform: uppercase;
              &:hover{
                color: $color_primary_yellow;
              }
            }
          }
        }  
      } // end of .entry-meta
    } // end of .entry-header
    .entry-content{
      margin-top: 40px;
      p{
        margin-bottom: 40px;
      }
      a.btn-black-border{
        color: black;
        &:hover{
          color: white;
        }
      }
    } // end of .entry-content
  } // end of .post
} // end of .blog, .single-post


/* Category page, Tag page, Month archives page, Search page - page header style*/
.archive, .search{
  .site-main{
    .page-header{
      border-bottom: $solid_border; 
      margin-bottom: 50px;
      h1{
        margin: 0;
        margin-bottom: 40px;
        line-height: 2.3rem;
      }
    }
  } 
}


/* Singal post page */
.single-post{
  .post{
    .entry-footer{
      font-family: $font_accent;
      text-transform: uppercase;
      span{
        margin-right: 20px;
      }
    }
  }
} // end of .single-post


/* Find us page */
.page{
  .site-main{
    .type-page{
      .entry-header{
        h1{
          margin-top: 0;
          margin-bottom: 50px;
          line-height: 2.3rem;
        }
      }
      .entry-content{
        h2{
          font-weight: 700;
          font-size: 2.1rem;
          line-height: 2.5rem;
          margin-top: 50px;
        }
      }
    }
  } // end of .site-main
} // end of .page


/* About page */
.page-template-about{
  .custom-hero{
    height:100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    h1{
      display: inline-block;
      color:white;
    }
  }
  .entry-content{
    width: 800px;
    margin: 80px auto;
    h2{
      font-weight: 700;
      font-size: 2.1rem;
      line-height: 2.5rem;
      margin-top: 50px;
      text-align: center;
    }
  }
} // end of .page-template-about


/* Front page */
.home{
  .site-content{
    width: 100%;
    margin: 0 auto;
    .front-page-hero{
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100vh;
      background: linear-gradient( rgba(0, 0, 0, 0.3),rgba(0, 0, 0, 0.5)), url('../../images/front-page-hero.jpg') no-repeat top/100%;
    }
    .front-page-shop,
    .front-page-latest-journals,
    .front-page-adventures{
      width: 1120px;
      margin: 80px auto;
      h2{
        text-align: center;
      }

      // Front page - Shop
      .shop-blocks{
        display: flex;
        .shop-blocks-wrapper{
          flex-basis: 25%;
          border: $solid_border;
          border-top-width: 5px;
          padding: 15px;
          text-align: center;
          &:hover{
            border-color: $color_primary_orange;
          }
          &:not(:last-of-type){
            margin-right: 15px;
          }
          img{
            width: 70px;
            height: 70px;
            display: block;
            margin: 30px auto;
          }
          a{
            color: white;
          }
        }
      } // end of .shop-blocks

      // Front page - Journal
      .journals-blocks{
        display: flex;
        .journals-blocks-wrapper{
          flex-basis: 33.33%;
          border: $solid_border;
          &:not(:last-of-type){
            margin-right: 15px;
          }
          .journals-blocks-thumbnail{
            width: 100%;
            height: 250px;  
            border-bottom: 5px solid #e1e1e1;
            overflow: hidden;
            img{
              height: auto;
              min-height: 100%;
              width: auto;
              min-width: 100%;
            }
          }
          .journals-blocks-info{
            padding: 30px 20px;
            font-size: 0.75rem;
            background-color: white;
            h3{
              font-size: 1.3rem;
              line-height: 1.8rem;
              margin-top: 15px;
              margin-bottom: 30px;
            }
            a{
              color: $color_primary_green;
              &:hover{
                color: $color_primary_purple;
              }
            }
          } // end of .journals-blocks-info
        }
      }  // end of .journals-blocks
      .adventures-blocks{
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-template-rows: 1fr 1fr;
        .adventures-blocks-wrapper img{
          display: block;
        }
        .adventures-blocks-info{
          h3{
            font-size: 2.4rem;
            line-height: 3rem;
            margin: 20px 0;
          }
        }
        .adventures-blocks-wrapper:nth-child(1){
          grid-row-start: span 2;
          grid-column-start: span 2;
          height: 552.5px;
          img{
            height: 552.5px; 
          }
        }
        .adventures-blocks-wrapper:nth-child(2){
          grid-column-start: span 2;
          height: 268.75px; 
          img{
            height: 268.75px; 
          }
        }
        .adventures-blocks-wrapper:nth-child(3){
          height: 268.75px; 
          img{
            height: 268.75px; 
          }
          .adventures-blocks-info{
            h3{
              font-size: 1.6rem;
              line-height: 2.2rem;
              margin: 20px 0;
            }
          }
        }
        .adventures-blocks-wrapper:nth-child(4){
          height: 268.75px; 
          img{
            height: 268.75px; 
          }
          .adventures-blocks-info{
            h3{
              font-size: 1.6rem;
              line-height: 2.2rem;
              margin: 20px 0;
            }
          }
        }     
      } // end of .adventures-blocks
    } 
    .btn-more-adventure{
      color: white;
    }
    .front-page-adventures{
      margin-bottom: 100px;
    }
  } // end of .site-content
} // end of .home

// adventure front page and archive style
.adventures-blocks, .adventure-grid{
  display: grid;
  grid-column-gap: 15px;
  grid-row-gap: 7px;
}
.adventures-blocks-wrapper, .adventure-grid-item{
  position: relative;
  a{
    display: inline-block;
  }
  img{
    display: inline-block;
    object-fit: cover;
    width: 100%; 
    filter: contrast(80%) brightness(70%);
  }
}
.adventures-blocks-info, .adventure-item-info{
  position: absolute;
  top: 0;
  margin: 25px;
  .entry-title a{
    font-family: $font__main;
    font-weight: 400;
    text-transform: none;
    color: white;
    &:hover{
      color: $color_primary_yellow;
    } 
  }
  .btn-white-border{
    color: white;
  }
}


// Global - pages without sidebar
/* Shop page, Single product page, Adventure page*/
.post-type-archive-product, .single-product, .tax-product-type, .post-type-archive-adventure{
  .site-content{
    width: 1120px;
    margin: 0 auto;
    padding-top: 50px;
    .content-area{
      padding-top: 60px;
      padding-bottom: 60px;
    }
  }
}

/* Shop page style */
.post-type-archive-product, .tax-product-type{
  .site-main{
    .page-header{
      text-align: center;
      border-bottom: $dashed_border;
      padding-bottom: 20px;
      ul{
        list-style: none;
        padding-left: 0;
        margin-left: 0;
        li{
          display: inline-block;
        }
        p{
          display: inline-block;
          margin: 10px auto;
          font-size: 1.2rem;
          padding-left: 30px;
          padding-right: 30px;
        }
      }
      
    }
    .product-grid{
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-column-gap: 15px;
      margin-bottom: 30px;
      grid-row-gap: 15px;
      .product-grid-item{
        border: $solid_border;
        .product-item-thumbnail{
          overflow: hidden;
          height: 250px;
          border-bottom: 5px solid #e1e1e1;
          img{
            height: 250px;
            width: auto;
            object-fit: cover;
          }
        }
        .product-item-info{
          background-color: white;
          border-top-width: 5px;
          position: relative;
          height: 55px;
          .line{
            display: inline-block;
            width: 100%;
            position: absolute;
            top: 27px;
            border-bottom: 2px dotted black;
          }
          h2{
            display: inline-block;
            font-size: 1rem;
            font-family: $font__main;
            text-transform: none;
            letter-spacing: 0;
            background-color: white;
          }
          .entry-title{
            position: absolute;
            padding-left: 20px;
            padding-right: 5px;
          }
          .price{
            position: absolute;
            right: 0;
            padding-right: 20px;
            padding-left: 5px;
          }
          
        }
      }
    } // end of .product-grid
  } // end of .site-main
} // end of .post-type-archive-product


/* Single product page style */
.single-product{
  article{
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 50px;
    .single-product-image{
      width: 100%;
    }
    .single-product-content{
      .entry-header h1{
        color: $color_primary_orange;
        margin-top: 0;
        margin-bottom: 0;
        line-height: 3rem;
      }
      .entry-content{
        margin-top: 0;
        .single-product-price{
          font-family: $font_accent;
          font-size: 1.8rem;
          margin-top: 20px;
          margin-bottom: 20px;
        }
      }  
    }
  } // end of article
} // end of .single-product

// social button style
.single-product, .single-post, .single-adventure{
  .social-button{
    margin-top: 50px;
    button{
      height: 45px;
      padding-top: 9px;
      margin-right: 10px;
    }
  }
}

// single-post style
.single-post{
  .post{
    margin-bottom: 0;
  }
  .social-button{
    margin-bottom: 80px;
  }
}


/* error 404 page */
.error404{
  .site-main{
    .error-404{
      .page-header{
        h1{
          font-size: 2.3rem;
          margin-top: 0;
          margin-bottom: 50px;
          line-height: 2.3rem;
        }
      }
      .search-form{
        fieldset{
          margin-left: 0;
        }
        .search-icon{
          display: none;
        }
        label{
          margin-left: 0;
        }
        
      }
      .page-content{
        .widget{
          .widgettitle{
          font-weight: 700;
          font-size: 1.8rem;
          line-height: 2.5rem;
          margin-top: 50px;
          }
          ul{   
            list-style: none;
            padding-left: 0;
            margin-left: 0;
            li{
              a{
                font-family: $font__main;
                text-transform: none;
              }
            }
          }
        }
      } // end of .page-content
    } // end of .error-404
  } // end of .site-main
} // end of .error404


/* Search page */
.search{
  .site-main{
    article{
      margin-bottom: 80px;
      .entry-header h2{
        line-height: 3rem;
        margin-bottom: 20px;
        a{
          font-weight: 700;
        }
      }
      .entry-summary{
        margin-top: 20px;
      }
      &:last-of-type{
        padding-bottom: 50px;
        margin-bottom: 50px;
        border-bottom: $solid_border;
      }
    }
  }
}

/* Adventure page */
.post-type-archive-adventure{
  .content-area{
    .page-header{
      text-align: center;
      border-bottom: $dashed_border;
      padding-bottom: 20px;
    }
    .adventure-grid{
      grid-template-columns: repeat(2, 1fr);
      margin-bottom: 30px;
      .adventure-grid-item{
        img{
          height: 552.5px;  
        }
      }
    }
  }
} // end of .post-type-archive-adventure

/* Single adventure page */
.single-adventure{
  .site-content{
    margin-bottom: 80px;
  }
  img.single-adventure-image{
    height:100vh;
    width: 100vw;
  }
  .single-adventure-content{
    width: 800px;
    margin: 0 auto;
    margin-top: 60px;
    h1{
      font-size: 2.7rem;
      line-height: 3rem;
    }
    h2{
      font-size: 1.2rem;
      color: black;
    }
  }
}